import Vue from 'vue'
import Router from 'vue-router'
import PageHome from '../pages/PageHome'
import PageNotFind from '../pages/PageNotFind'
import PageDeskTop from '../pages/PageDeskTop'
import PageMonitorData from '../pages/PageMonitorData'
import PageInspectView from '../pages/PageInspectView'
import PageConfig from '../pages/PageConfig'
import PageSupport from '../pages/PageSupport'
import PageAlarm from '../pages/PageAlarm'
import PageLog from '../pages/PageLog'
Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    redirect: '/home',
  }, {
    path: '/home',
    name: 'home',
    redirect: '/home/desktop',
    component: PageHome,
    children: [{
      path: 'not-find',
      component: PageNotFind
    }, {
      path: 'desktop',
      component: PageDeskTop
    }, {
      path: 'data',
      component: PageMonitorData
    }, {
      path: 'inspect-view',
      component: PageInspectView
    }, {
      path: 'log',
      component: PageLog
    }, {
      path: 'support',
      component: PageSupport
    }, {
      path: 'alarm',
      component: PageAlarm
    }, {
      path: 'config',
      component: PageConfig
    }]
  }]
})

router.beforeEach((to, from, next) => {
  if (to.matched.length == 0) {
    next({
      path: '/home/not-find'
    })
  } else {

    next()
  }
})

export default router



