<template>
  <div class="wrapper-box">
    <aside>
      <header>导航</header>
      <ZlTree :data="nav" @node-click="onNodeClick" :currentNode="currentNode" parent-clickable></ZlTree>
    </aside>
    <section class="box tab-box">
      <header>
        <ul>
          <li v-for="(tab,index) in showTabs">
            <a :class="{selected : index == currentPage}" @click="currentPage=index"><span>{{tab}}</span></a>
          </li>
        </ul>
      </header>
      <section>
        <!-- <keep-alive> -->
        <component :is="componentName" :node="currentNode"  @choose-item="onChooseItem"></component>
        <!-- </keep-alive> -->
      </section>
    </section>
  </div>
</template>
<script>
import ZlTree from "../components/ZlTree";
import TunnelState from "../components/monitor_data/state/TunnelState";
import GILState from "../components/monitor_data/state/GILState";

import WireState from "../components/monitor_data/state/WireState";
import EnvrState from "../components/monitor_data/state/EnvrState";
import GaurdState from "../components/monitor_data/state/GaurdState";
import CameraState from "../components/monitor_data/state/CameraState";
import TxtRealData from "../components/monitor_data/real_data/TxtRealData";
import GaugeRealData from "../components/monitor_data/real_data/GaugeRealData";
import ChartTableHisData from "../components/monitor_data/his_data/ChartTableHisData";
import { MONITOR_TYPES, MONITOR_PARAMS } from "../json/json_base_info";
import {
  TUNNELS,
  WIRES,
  SECTIONS,
  MONITOR_DEVICES,
  MONITOR_CAMERAS
} from "../json/json_device_info";

export default {
  components: {
    ZlTree,
    TunnelState,
    GILState,
    WireState,
    EnvrState,
    GaurdState,
    CameraState,
    TxtRealData,
    GaugeRealData,
    ChartTableHisData
  },
  data() {
    return {
      tabs: ["状态总览", "实时数据", "历史数据"],
      currentPage: 0,
      nav: [],
      currentNode: {}
    };
  },
  computed: {
    showTabs() {
      if (this.currentNode.isLeaf) {
        return this.tabs;
      } else {
        return [this.tabs[0]];
      }
    },
    componentName() {
      if (this.currentNode.type == "CAMR_MONITOR") {
        return "CameraState";
      } else if (!this.currentNode.isLeaf) {
        return "TunnelState";
      } else if (this.currentNode.type == "GIL") {
        return this.currentPage == 0
          ? "GILState"
          : this.currentPage == 1 ? "GaugeRealData" : "ChartTableHisData";
      } else if (this.currentNode.type == "WIRE") {
        return this.currentPage == 0
          ? "WireState"
          : this.currentPage == 1 ? "GaugeRealData" : "ChartTableHisData";
      } else if (this.currentNode.type == "SECTION") {
        return this.currentPage == 0
          ? this.currentNode.monitor_type_name =='ENVR'?"EnvrState":"GaurdState"
          : this.currentPage == 1 ? "GaugeRealData" : "ChartTableHisData";
      }
    }
  },
  mounted() {
    /*计算左侧树形结构*/
    this.axios.get("device-tree").then(response=>{
      let tunnels = response.data['tunnels'] 
      let wires  = response.data['wires'] 
      let monitor_types  = response.data['monitor_types'] 
      let sections  = response.data['sections'] 
      tunnels.map((tunnel, index) => {
        let node = {
          name: tunnel.name,
          type: "tunnel",
          label: tunnel.name_cn,
          children: []
        };

            this.nav.push(node);
        if (index == 0) {
          node.defaultSelected = true;
        }
          monitor_types.map(monitor_type => {
            let subnode = {
              icon: monitor_type.icon,
              name: monitor_type.name,
              label: monitor_type.name_cn,
              type: monitor_type.type,
              children: []
            };
            if (monitor_type.type == "WIRE_MONITOR") {
              wires.map(wire => {
                if (wire.type == "WIRE") {
                  subnode.children.push({
                    name: wire.name,
                    label: wire.name_cn,
                    type: wire.type,
                    monitor_type_name: monitor_type.name
                  });
                }
              });
            } else if (monitor_type.type == "GIL_MONITOR") {
              wires.map(wire => {
                if (wire.type == "GIL") {
                  subnode.children.push({
                    name: wire.name,
                    label: wire.name_cn,
                    type: wire.type,
                    monitor_type_name: monitor_type.name
                  });
                }
              });
            } else if (monitor_type.type == "SECTION_MONITOR") {
              sections.map(section => {
                subnode.children.push({
                  name: section.name,
                  label: section.name_cn,
                  type: "SECTION",
                  monitor_type_name: monitor_type.name,
                  img_url: section.img_url
                });
              });
            } else if (monitor_type.type == "CAMR_MONITOR") {
              // MONITOR_CAMERAS.map(camera => {
              //   subnode.children.push({
              //     name: camera.name,
              //     label: camera.name_cn,
              //     type: "CAMERA",
              //     location: camera.location
              //   });
              // });
            }
            if (subnode.children && subnode.children.length > 0) {
              node.children.push(subnode);
            }
          });
      });
    })

  },
  methods: {
    onNodeClick(node) {
      if (!node.isLeaf) {
        this.currentPage = 0;
      }
      this.currentNode = node;
    },
    onChooseItem(node) {
      this.currentNode = node;
    }
  }
};
</script>
<style scoped>
aside,
.box {
  height: 100%;
  background-color: #0b3567;
  border: 1px solid #3f6aa1;
  border-radius: 5px;
}
aside > header,
.box > header {
  height: 36px;
  font-size: 16px;
  line-height: 36px;
  overflow: hidden;
  position: relative;
}

aside {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 250px;
  margin-right: 2px;
  overflow-y: auto;
}

aside > header {
  padding-left: 5px;
  border-bottom: 1px solid #3f6aa1;
}

.tab-box {
  position: absolute;
  left: auto;
  right: 2px;
  width: calc(100% - 254px);
}

.tab-box > header {
  margin-left: 5px;
}

.tab-box > header li {
  float: left;
}

.tab-box > header a {
  position: relative;
  height: 0;
  display: inline-block;
  padding: 0 30px;
  border-bottom: 36px solid #75a7c4;
  border-left: 18px solid transparent;
  border-right: 18px solid transparent;
  margin-left: -18px;
}

.tab-box > header a:before {
  position: absolute;
  content: "";
  right: -18px;
  top: 0;
  border-bottom: 36px solid #ccc;
  border-right: 18px solid transparent;
  z-index: 2;
}

.tab-box > header a:after {
  position: absolute;
  content: "";
  right: -17px;
  top: 1px;
  border-bottom: 35px solid #75a7c4;
  border-right: 17px solid transparent;
  z-index: 2;
}

.tab-box > header a:hover,
.tab-box > header a.selected {
  border-bottom: 36px solid #cfdee9;
  color: #3c3c3c;
  z-index: 3;
}

.tab-box > header a:hover::after,
.tab-box > header a.selected::after {
  position: absolute;
  content: " ";
  right: -17px;
  top: 1px;
  border-bottom: 35px solid #cfdee9;
  border-right: 17px solid transparent;
  z-index: 3;
}

.left-box > section,
.tab-box > section {
  position: absolute;
  top: 36px;
  bottom: 0;
  left: 5px;
  right: 0;
  overflow-y: auto;
}

.tab-box > section {
  background-color: #cedde8;
  padding-top: 20px;
}
</style>



